import Vue from 'vue';
import Router from 'vue-router';
import { store } from './store';

import { HomePage, LoginPage, BasesPage, TeamsPage, UsersPage, RegisterPage } from './components/pages';

Vue.use(Router);

export const router = new Router({
  mode: 'history',
  routes: [
    { path: '/', component: HomePage, meta: { requiresAuth: true } },
    { path: '/login', component: LoginPage },
    { path: '/bases', component: BasesPage, meta: { requiresAuth: true } },
    { path: '/register', component: RegisterPage, meta: { requiresAuth: true } },
    { path: '/teams', component: TeamsPage, meta: { requiresAuth: true } },
    { path: '/users', component: UsersPage, meta: { requiresAuth: true } },

    // otherwise redirect to home
    { path: '*', redirect: '/' }
  ]
});

// authorisation guard
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (store.getters.loggedIn) {
            next();
            return;
        }
        next('/login');
    } else {
        next();
    }
});
