<script>
import { Mutation } from '../../store/types';

export default {
  name: 'RegisterPage',
  data: function() {
      return {
          firstName: {
              value: '',
              error: '',
              showError: false
          },
          lastName: {
              value: '',
              error: '',
              showError: false
          },
        email: {
            value: '',
            error: '',
            showError: false
        },
        password: {
            value: '',
            error: '',
            showError: false
        },
        error: null,
      }
  },
  mounted: function() {
    this.$parent.$refs.loader.hideLoader();
  },
  created: function() {
      this.$store.commit(`${Mutation.NAV_SHOW_TABLES}`, false);
  },
  methods: {
     
      register() {
        this.$parent.$refs.loader.showLoader(); 
        this.$myAuth.registerUser(this.email.value, this.password.value,
            this.firstName.value, this.lastName.value, (err, result) => {
                if (result) {
                    this.$router.push({path: '/login' });
                }
            });
      }
  }
}
</script>

