<template>
<div>
  <my-flex-container>
    <my-box>
      <h2>Teams</h2>
      <div class="div-block-6">
          <div>
            <div class="dropdown-label">View</div>
            <div data-hover="" data-delay="0" class="dropdown w-dropdown">
              <div class="dropdown-toggle w-dropdown-toggle">
                <div>Default (All)</div><img src="../../assets/images/arrow_drop_down_24px-gray.svg" alt=""></div>
              <nav class="dropdown-list w-dropdown-list"><a href="#" class="w-dropdown-link">An item with a really really really really long name</a><a href="#" class="w-dropdown-link">Link 2</a><a href="#" class="w-dropdown-link">Link 3</a></nav>
            </div>
          </div>
          <div class="flex-horizontal">
            <div class="filter"><img src="../../assets/images/filter_list_24px.svg" alt="" class="filter-icon">
              <div>Filtered by <strong>None</strong></div>
            </div>
            <div class="filter-active"><img src="../../assets/images/import_export_24px.svg" alt="" class="filter-icon">
              <div>Sorted by <strong>Last modified</strong></div>
            </div>
          </div>
      </div>
    </my-box>
  </my-flex-container>
  <div>
    <my-table-heading>Teams</my-table-heading>
    <my-white-container>
      <my-table
        :key="loading"
        v-bind:headers="getHeaders()"
        v-bind:tableItems="getTableItems()">
      </my-table>
    </my-white-container>
  </div>
</div>
</template>
<script>
import { mapState } from 'vuex';
import { Action, Mutation } from '../../store/types';

export default {
  name: 'BasesPage',
  components: {
    MyFlexContainer: () => import('../library/MyFlexContainer'),
    MyWhiteContainer: () => import('../library/MyWhiteContainer'),
    MyTableHeading: () => import('../library/MyTableHeading'),
    MyTable: () => import('../library/MyTable'),
    MyBox: () => import('../library/MyBox')
  },
  computed: {
      ...mapState({
        data: state => state.data,
        loading: state => state.loading
      })
  },
  created () {
      // this.getTables(1, 10);
      this.$store.commit(`${Mutation.NAV_SHOW_TABLES}`, false);
      this.$store.dispatch(Action.GET_TEAMS, ['teamName','createdAt','updatedAt']);
  },
  methods: {
    getHeaders() {
      return this.data.headers;
    },
    getTableItems() {
      return this.data.rows;
    }
  },
}
</script>
