import Vue from 'vue';
import Vuex from 'vuex';
import VuexPersistence from 'vuex-persist';
import localForage from 'localforage';
import { Action, Mutation } from './types';
import { router } from '../router';
import { dayjs } from 'dayjs';

const vuexLocal = new VuexPersistence({
    storage: window.localStorage,
    key: 'rebaseStorage'
});

Vue.use(Vuex);

function createState() {
    return {
        user: null,
        alert: null,
        loading: false,
        navigation: {
            showTables: false,
            tables: []
        },
        team: {
            dataItems: {},
            tables: {},
            teams: {},
            users: {}
        },
        data: {}
    };
}

export const store = new Vuex.Store({
    state: createState(),
    actions: {
        [Action.LOADING_STATUS]({ commit }, loadingStatus) {
            commit(Mutation.LOADING_STATUS, loadingStatus)
        },
        [Action.LOGIN]({ commit }, user) {
            commit(Mutation.LOADING_STATUS, true);
            window['ts'].users.authenticateUser(user.email, user.password)
                .then(
                    users => {
                        commit(Mutation.LOGIN, users);
                        commit(Mutation.LOADING_STATUS, false);
                        router.push('/');
                    });
        },
        [Action.LOGOUT]({ commit }) {
            commit(Mutation.LOADING_STATUS, true);
            window['ts'].users.logout()
                .then(() => {
                    commit(Mutation.LOGOUT);
                    commit(Mutation.LOADING_STATUS, false);
                    router.push('/login');
                });
        },
        [Action.SET_ACTIVE_TABLE]({ dispatch, commit }, index) {
            for (var i=0; i<this.state.user.tables.length; i++) {
                commit(Mutation.SET_ACTIVE_TABLE, { index: i, active: (index == i) });
            }

            if (this.state.user.tables.length > 0) {
                commit(Mutation.LOADING_STATUS, true);

                commit(Mutation.RESET_DATA);

                const id = this.state.user.tables[index].id;
                window['ts'].token = this.state.user.token;
                window['ts'].tables.getTable(id)
                    .then(table => {
                        commit(Mutation.APPEND_FIELDS_ACTIVE_TABLE, table);
                        dispatch(Action.GET_DATA_ITEMS, table.tableName, 1, 10);
                    });
            }
        },
        [Action.GET_DATA_ITEMS]({ commit }, table, page, pageSize) {
            window['ts'].token = this.state.user.token;
            commit(Mutation.LOADING_STATUS, true);
            window['ts'].dataItems.getTableDataItems(table, page, pageSize)
                .then(dataItems => {
                    commit(Mutation.GET_DATA_ITEMS, dataItems);
                    commit(Mutation.MAP_DATA, dataItems);
                    commit(Mutation.LOADING_STATUS, false);
                });
        },
        [Action.GET_TABLES]({ commit }, headers) {
            window['ts'].token = this.state.user.token;
            commit(Mutation.LOADING_STATUS, true);
            commit(Mutation.RESET_DATA);
            window['ts'].tables.getTables()
                .then(tables => {
                    commit(Mutation.GET_TABLES, tables);
                    commit(Mutation.MAP_HEADERS, headers);
                    commit(Mutation.MAP_DATA, tables);
                    commit(Mutation.LOADING_STATUS, false);
                });
        },
        [Action.GET_TEAMS]({ commit }, headers) {
            window['ts'].token = this.state.user.token;
            commit(Mutation.LOADING_STATUS, true);
            commit(Mutation.RESET_DATA);
            window['ts'].teams.getTeams()
                .then(teams => {
                    commit(Mutation.GET_TEAMS, teams);
                    commit(Mutation.MAP_HEADERS, headers);
                    commit(Mutation.MAP_DATA, teams);
                    commit(Mutation.LOADING_STATUS, false);
                });
        },
        [Action.GET_USERS]({ commit }, headers) {
            window['ts'].token = this.state.user.token;
            commit(Mutation.LOADING_STATUS, true);
            commit(Mutation.RESET_DATA);
            window['ts'].users.getUsers()
                .then(users => {
                    commit(Mutation.GET_USERS, users);
                    commit(Mutation.MAP_HEADERS, headers);
                    commit(Mutation.MAP_DATA, users);
                    commit(Mutation.LOADING_STATUS, false);
                });
        }
    },
    mutations: {
        [Mutation.LOGIN](state, payload) {
            state.user = payload;
        },
        [Mutation.LOGOUT](state) {
            state.user = null;
            state.loading = false;
            state.navigation.showTables = false;
            state.navigation.activeTable = {};
            // state.navigation.tables = [];
            state.team.home = { data: {} };
            state.team.tables = {};
            state.team.teams = {};
            state.team.users = {};
        },
        [Mutation.SET_ACTIVE_TABLE](state, {index, active}) {
            state.user.tables[index].active = active;
            if (active) {
                state.navigation.activeTable = state.user.tables[index];
            }
        },
        [Mutation.APPEND_FIELDS_ACTIVE_TABLE](state, table) {
            state.navigation.activeTable.fieldNames = 
                [...table.fieldNames.split(','), 'CreatedAt', 'UpdatedAt', 'Id'];
            state.data.headers = state.navigation.activeTable.fieldNames;                
        },
        [Mutation.GET_DATA_ITEMS](state, payload) {
            state.team.dataItems = payload;
        },
        [Mutation.RESET_DATA](state) {
            state.data = {};
        },
        [Mutation.MAP_HEADERS](state, headers) {
            state.data.headers = headers
        },
        [Mutation.MAP_DATA](state, payload) {
            // map the data
            state.data.rows = payload.data.map(item => {
                var row = {};
                for (var i=0; i<state.data.headers.length; i++) {
                    row[state.data.headers[i]] = item[state.data.headers[i]];
                }
                return row;
            });
        },
        [Mutation.GET_TABLES](state, payload) {
            state.team.tables = payload;
        },
        [Mutation.GET_TEAMS](state, payload) {
            state.team.teams = payload;
        },
        [Mutation.GET_USERS](state, payload) {
            state.team.users = payload;
        },
        [Mutation.LOADING_STATUS](state, loadingStatus) {
            state.loading = loadingStatus;
        },
        [Mutation.NAV_SHOW_TABLES](state, showTables) {
            state.navigation.showTables = showTables;
        }
    },
    getters: {
        loggedIn: state => !!state.user,
        token: state => state.token,
        tables: state => (state.user) ? state.user.tables : []
    },
    plugins: [vuexLocal.plugin]
});

