<template>
<body class="bg-grey-lighter h-screen font-body">
    <div class="container mx-auto h-full flex justify-center items-center">
        <div class="w-5/12">
            <svg class="h-20 w-24 content-center mx-auto"  fill="none" xmlns="http://www.w3.org/2000/svg">
                <rect width="8" height="61" fill="#575EFF"/>
                <circle cx="28" cy="14" r="10" stroke="#6A36FF" stroke-width="8"/>
                <circle cx="30" cy="47" r="10" stroke="#57C2FF" stroke-width="8"/>
                <rect x="14" y="41" width="17" height="20" fill="#F5F5F5"/>
                <rect x="20" y="33" width="11" height="8" fill="#57C3FF"/>
                <rect x="24" y="53" width="7" height="8" fill="#57C3FF"/>
                <path d="M14 33H22L31 61H23L14 33Z" fill="#57C3FF"/>
            </svg>
            <h1 class="font-hairline mb-6 text-center text-3xl tracking-widest">Sign in to Rebase</h1>
            <form @submit.prevent="handleSubmit">
            <div class="border-teal p-8 border-t-12 bg-white mb-6 rounded-lg shadow-lg">
                <div class="mb-4 ">
                    <label class="font-bold text-grey-darker block mb-2 w-2/3 mx-auto h-full justify-center items-center" for="email">Email</label>
                    <input v-model="email" type="email" class="block appearance-none bg-white border border-grey-light hover:border-grey px-2 py-2 rounded shadow w-2/3 mx-auto h-full justify-center items-center" placeholder="Your email">
                </div>

                <div class="mb-4">
                    <label class="font-bold text-grey-darker block mb-2 w-2/3 mx-auto h-full justify-center items-center">Password <a href="#" class="no-underline text-indigo-500 float-right font-normal text-sm">Forgot password?</a> </label> 
                    <input v-model="password" type="password" class="block appearance-none  bg-white border border-grey-light hover:border-grey px-2 py-2 rounded shadow w-2/3 mx-auto h-full justify-center items-center" placeholder="Your Password">
                </div>
                <div class="mb-4">
                <button class="bg-indigo-500 hover:bg-indigo-700 text-white font-bold py-2 px-4 rounded w-2/3 mx-auto h-full flex justify-center items-center">
                  Sign in with Email
                </button>
                </div>
                <div class="text-center pt-6">
                  <p class="text-grey-dark text-sm">Don't have an account?  <a href="#" v-on:click="register()"  class="no-underline text-indigo-500 font-bold">Signup for free</a>.</p>
                </div>
            </div>
            </form>
        </div>
    </div>
</body>
</template>

<script>
import { mapActions } from 'vuex';

// import { mapActions } from 'vuex';
import { Action, Mutation } from '../../store/types';

export default {
  name: 'LoginPage',
  data: function() {
      return {
        email: '',
        password: '',
        submitted: false,
      }
  },
  created: function() {
    this.$store.commit(`${Mutation.NAV_SHOW_TABLES}`, false);
    this.$store.commit(Mutation.LOADING_STATUS, false);
  },
  methods: {
    ...mapActions({
      login: [Action.LOGIN]
    }),
//    ...mapActions('', [Action.LOGIN])
    // ...mapActions('users', ['authenticateUser']),
    // ...mapActions('loading', {
    //   showLoader: 'show'
    // }),
    handleSubmit() {
      this.submitted = true;
      const { email, password } = this;
      if (email && password) {
        //this.login({ email, password });
        // this.showLoader();
        // this.authenticateUser({ email, password });
        this.$store.dispatch(`${Action.LOGIN}`, { email, password})
        //   .then(() => this.$router.push('/'));
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
