<template>
  <div id="app">
    <Navbar v-if="loggedIn"></Navbar>
    <!-- <div v-if="alert.mesage" :class="`alert ${alert.type}`">{{alert.message}}</div> -->
    <router-view></router-view>
    <Loader v-if="loading"></Loader>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'app',
  data: function() {
    return {
      loaded: false
    }
  },
  mounted() {
    // load the TableServiceClientApi library
    const script = document.createElement('script');

      if (window['ts']) { return; }

      if (window.location.href.startsWith('http://localhost')) {
        script.setAttribute('src', 'https://localhost:5001/js/tableservice-api.js');
      } else {
        script.setAttribute('src', 'https://appcore.buildwithrebase.online/js/tableservice-api.js')
      }
      script.setAttribute('async', false);
      script.setAttribute('defer', false);

      document.body.appendChild(script);
      
      // eslint-disable-next-line
      document.body.addEventListener('ts_loaded', function($ev) {
        // eslint-disable-next-line
       console.log('ts_loaded', window['ts']['baseUrl'], window['ts'].isAuthenticated() ? 'authenticated' : 'not authenticated');
       
       window['ts'].setRaiseEvent(false);
      });

//      this.hideLoader();
  },
  components: {
    Navbar: () => import('./components/layout/Navbar'),
    Loader: () => import('./components/layout/Loader')
  },
  computed: {
    ...mapState({
      loggedIn: state => (state.user != null),
      loading: state => state.loading
    })
    // ...mapState({
    //   alert: state => state.alert,
    //   users: state => state.users,
    //   loading: state => state.loading
    // })
  },
  // watch:{
  //   $route (to, from){
  //     this.hideLoader();
  //   }
  // },
  methods: {
    // ...mapActions({
    //   clearAlert: 'alert/clear'
    // }),
    // ...mapActions('loading', {
    //   hideLoader: 'hide'
    // })
  }
}
</script>

<style src="./assets/tailwind.css"></style>
